<script>
export default {
  components: {
  },
  data: () => ({
    form: false,
    dialogSuccess: false,
    mail: {
      name: '',
      phone: '',
      email: '',
      message: ''
    },
    rules: {
      email: value => {
        const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
        return pattern.test(value) || 'Correo electrónico inválido'
      }
    }
  }),
  methods: {
    sendEmail () {
      var emailjs = window.emailjs
      emailjs.send('service_cq91poe', 'template_5648t99', this.mail).then(response => {
        console.log('Success!', response.status, response.text)
        this.shirtQuoteDialog = false
        this.dialogSuccess = true
        this.mail = {
          name: '',
          phone: '',
          message: '',
          email: ''
        }
      }, error => {
        console.log('Failed...', error)
      })
    }
  },
  mounted () {
  }
}
</script>
